* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .header {
    background-color: #4CAF50;
    color: white;
    padding: 20px;
    text-align: center;
    width: 100%;
  }
  
  .main-content {
    flex: 1;
    padding: 20px;
  }
  
  .input-section, .feedback-section {
    margin-bottom: 20px;
  }
  
  .textarea {
    width: 100%;
    height: 150px;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
  }
  
  .file-upload {
    margin-bottom: 20px;
  }
  
  .file-label {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .file-input {
    display: none;
  }
  
  .submit-btn {
    display: inline-block;
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 17px;
  }
  
  .feedback-section textarea {
    height: 100px;
  }
  
  .footer {
    background-color: #4CAF50;
    color: white;
    padding: 10px;
    text-align: center;
    width: 100%;
  }
  
  @media (max-width: 768px) {
    .header, .footer {
      padding: 15px;
      font-size: 18px;
    }
  
    .submit-btn {
      font-size: 15px;
      padding: 8px 16px;
    }
  
    .textarea {
      height: 120px;
    }
  }
  